import vCheckbox from '../../../components/vCheckbox.vue'

let $self
export default {
	components: {
		'v-checkbox': vCheckbox
	},
	data() {
		return {
			genderArr: [{ label: '男', gender: 1 }, { label: '女', gender: 0 }],
			signArr: [{ label: '家', field: 0 }, { label: '公司', field: 1 }, { label: '学校', field: 2 }],
			form: {
				name: '',
				gender: 1,
				mobile: '',
				address: '省份 城市 县区',
				addressDetail: '',
				latitude: '',
				longitude: '',
				sign: 0,
				defaultAddress: false
			},
			uuid: ''
		}
	},
	onLoad(options) {
		$self = this
		this.uuid = uni.getStorageSync('uuid')
		if (JSON.stringify(options) !== '{}') {
			let address = JSON.parse(decodeURIComponent(options.address))
			this.form = Object.assign({}, address)
		}
	},
	onShow() {
		let curAddress = uni.getStorageSync('curAddress')
		// console.log(curAddress)
		if (curAddress) {
			let tudeArr = curAddress.location.split(',')
			let longitude = tudeArr[0]
			let latitude = tudeArr[1]
			this.form = Object.assign(this.form, {
				latitude,
				longitude,
				address: curAddress.district,
				addressDetail: curAddress.name
			})
			uni.removeStorageSync('curAddress')
		}
	},
	methods: {
		checkGender(item) {
			this.form.gender = item.gender
		},
		checkSign(sign) {
			this.form.sign = sign.field
		},
		checkAddress() {
			uni.navigateTo({
				url: `../search/search?address=${this.form.addressDetail}`
			})
		},
		setDefault(defaultAdd) {
			this.form.defaultAddress = defaultAdd
		},
		submit() {
			let param = {
				address: this.form.address || '',
				addressDetail: this.form.addressDetail || '',
				defaultAddress: this.form.defaultAddress || '',
				latitude: this.form.latitude || '',
				longitude: this.form.longitude || '',
				mobile: this.form.mobile || '',
				name: this.form.name || '',
				sign: this.form.sign || '',
				userUuid: this.uuid
			}
			if (this.form.id) {
				param = Object.assign(param, {id: this.form.id})
			}

			uni.request({
				url: 'https://messagecome.com/user/address/edit',
				method: 'POST',
				data: param
			}).then(infoRes => {
				let [err, res] = infoRes
				if (res.data && res.data.status === 1) {
					let title = param.id ? '地址编辑成功' : '地址新增成功'
					uni.showToast({
						title: title,
						icon: 'success',
						duration: 1000
					})
					uni.navigateBack({})
				} else {
					uni.showToast({
						icon: 'none',
						title: res.data.errMsg,
						duration: 1000
					});
				}
			})
		},
		delAddress() {
			uni.showModal({
				title: '删除地址',
				content: '确认要删除改地址吗？',
				cancelColor: '#808080',
				confirmColor: '#1DA06E',
				success: function (res) {
					if (res.confirm) {
						uni.request({
							url: 'https://messagecome.com/user/address/del',
							// method: 'POST',
							data: {id: $self.form.id || 0}
						}).then(infoRes => {
							let [err, res] = infoRes
							if (res.data && res.data.status === 1) {
								uni.showToast({
									title: '删除成功',
									icon: 'success',
									duration: 1000
								})
								uni.navigateBack({})
							}
						})
					}
				}
			});
		}
	}
}
